import React, { useState, useRef, useLayoutEffect, useCallback, useEffect } from 'react';
import { useShortcuts } from './util';
import { motion } from 'framer-motion';

function Blooper({ containerWidth, containerHeight, speed }) {

  const [ loaded, setLoaded ] = useState(false);

  const [ { width, height }, setDims ] = useState({ width: 0, height: 0 });

  const maxX = containerWidth - width;
  const maxY = containerHeight - height;

  const [ x, setX ] = useState(0);
  const [ y, setY ] = useState(0);

  const [ hue, setHue ] = useState(0);
  const [ rot, setRot ] = useState(0);

  useEffect(() => {
    let [ localX, localY ] = [ Math.random() * maxX, Math.random() * maxY ];
    let [ vx, vy, vRot ] = [ (Math.random() > 0.5) ? speed : -speed, (Math.random() > 0.5) ? speed : -speed, -0.2 + 0.4 * Math.random() ];
    let localHue = 0;
    let localRot = 0;
    setX(localX);
    setY(localY);
    setHue(localHue);
    setRot(localRot);
    const update = () => {
      localX += vx;
      if (localX < 0 || localX > maxX) {
        localX = Math.min(Math.max(localX, 0), maxX);
        vx = -vx
        localHue += 27;
        vRot = -0.2 + 0.4 * Math.random();
      }
      localY += vy;
      if (localY < 0 || localY > maxY) {
        localY = Math.min(Math.max(localY, 0), maxY);
        vy = -vy;
        localHue += 27;
        vRot = -0.2 + 0.4 * Math.random();
      }
      localRot += vRot;
      setX(localX);
      setY(localY);
      setHue(localHue);
      setRot(localRot);
    }
    const interval = setInterval(update, 10);
    return () => clearInterval(interval);
  }, [ setX, setY, setHue, setRot, containerWidth, containerHeight, speed, maxX, maxY ]);

  return (
    <div className="blooper" style={{left: x, top: y, opacity: (loaded) ? 1 : 0, filter: `hue-rotate(${hue}deg)`, transform: `rotate(${rot}deg)` }}>
      <img
        src="/bloop/static/img/bloop.png"
        alt="Blooper"
        onLoad={(e) => { const { clientWidth, clientHeight } = e.target; setDims({width: clientWidth, height: clientHeight}); setLoaded(true); }}
      />
    </div>
  );
}

function Loading({ text }) {
  const parentVariants = {
    wave: {transition: {staggerChildren: 0.1}}
  };
  const childVariants = {
    wave: {scale: [1, 1.2, 1], transition: { ease: 'easeInOut', duration: 0.5, repeat: Infinity, repeatDelay: 1.5 }}
  };
  return (
    <motion.div key={text} variants={parentVariants} className="loading" animate="wave">
      {text.split('').map((letter, i) => {
        if (letter === ' ') letter = '\xa0';
        return (
          <motion.span key={i} variants={childVariants} style={{position: 'relative'}}>{letter}</motion.span>
        );
      })}
    </motion.div>
  );
}

function MessageEditor({ onSubmit }) {
  const [ text, setText ] = useState('');
  const ref = useRef(null);
  useLayoutEffect(() => {
    if (ref.current !== null) ref.current.select();
  }, [ref]);
  return (
    <form className="message-edit" onSubmit={(e) => { e.preventDefault(); onSubmit(text); }}>
      <input type="text" value={text} onChange={(e) => setText(e.target.value)} autoFocus ref={ref} />
    </form>
  );
}

export default function Wrapper() {
  const [ { width, height }, setDims ] = useState({ width: 0, height: 0 });
  const ref = useRef(null);
  const updateDimensions = useCallback(() => {
    if (ref.current !== null) {
      const { width, height } = ref.current.getBoundingClientRect();
      setDims({ width, height });
    }
  }, [ref, setDims]);
  useLayoutEffect(updateDimensions, [updateDimensions]);
  useEffect(() => {
    window.addEventListener('resize', updateDimensions);
    return () => window.removeEventListener('resize', updateDimensions);
  }, [updateDimensions]);

  const [ speed, setSpeed ] = useState(3);
  const [ loadingText, setLoadingText ] = useState('LOADING...');
  const [ editingText, setEditingText ] = useState(false);

  useShortcuts([
    {key: 'ArrowUp', fn: () => setSpeed(speed + 0.5)},
    {key: 'ArrowDown', fn: () => setSpeed(speed - 0.5)},
    {key: 'Space', fn: () => setEditingText(true)},
  ])

  return (
    <motion.div
      className="wrapper"
      ref={ref}
      animate={{background: ['linear-gradient(0deg, rgba(44,52,156,1) 0%, rgba(48,188,255,1) 100%)', 'linear-gradient(0deg, rgba(75,105,200,1) 0%, rgba(109,222,251,1) 100%)']}}
      transition={{ease: 'linear', duration: 5, yoyo: Infinity}}
    >
      <Loading text={loadingText} />
      {(editingText) && <MessageEditor onSubmit={(text) => { setLoadingText(text); setEditingText(false); }} />}
      <Blooper containerWidth={width} containerHeight={height} speed={speed} />
    </motion.div>
  );
}
